<script>
import {dataplay} from '@/utiils/player'
import player from '@/assets/mcplayer.mp4'
let bz='https://img-baofun.zhhainiao.com/fs/a725f45c52a1ceded968a1face25cc1b.jpg'
export default{
  data() {
    return {
      newDate: new Date(),
      dataplay:dataplay(player, bz),
      imagebox:[
        {id:0,img:require('@/assets/image/R-C0.jpg')},
        {id:1,img:require('@/assets/image/R-C1.jpg')},
        {id:2,img:require('@/assets/image/R-C2.jpg')},
        {id:3,img:'https://img-baofun.zhhainiao.com/fs/a725f45c52a1ceded968a1face25cc1b.jpg'},
      ],
    }
  },
// 挂载时间
  mounted () {
    let that = this
    this.timer = setInterval(function () {
      that.newDate = new Date().toLocaleString()
    })
  },
// 销毁时清除计时器
  beforeDestroy: function () {
    if (this.timer) {
      clearInterval(this.timer)
    }
  }
}
</script>

